






































export default {
	data() {
		return {
			id: '',
			deliveryInfo: {},
			info: {}
		};
	},
	onLoad(options) {
		console.log(options)
		this.id = options.id
		if (options.from) {
			this.getAfterDelivery()
		} else {
			this.getInfo()
		}
	},
	methods: {
		getInfo() {
			var that = this
			this.$api.get('/api/delivery/getDeliveryInfo', {
				order_id: this.id
			}, function(res) {
				if (res.data.code == 200) {
					console.log(res)
					var data = res.data.data
					that.info = data
					that.deliveryInfo = data.delivery
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				}
			})
		},
		getAfterDelivery() {
			var that = this
			this.$api.get('/api/aftersale/getDeliveryInfo', {
				aftersale_id: this.id
			}, function(res) {
				if (res.data.code == 200) {
					console.log(res)
					var data = res.data.data
					that.info = data
					that.deliveryInfo = data.delivery
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				}
			})
		},
	}
}
